import { FinancialOverviewChart } from "@/components/charts/financial-overview-chart";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";

const transactions = [
    { invoiceId: 'INV-001', patient: 'John Doe', amount: '$250.00', status: 'Paid' },
    { invoiceId: 'INV-002', patient: 'Jane Smith', amount: '$150.75', status: 'Pending' },
    { invoiceId: 'INV-003', patient: 'Bob Johnson', amount: '$45.50', status: 'Paid' },
    { invoiceId: 'INV-004', patient: 'Alice Williams', amount: '$800.00', status: 'Overdue' },
];

export default function FinanceDashboard() {
  return (
    <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
      <FinancialOverviewChart />
      <Card>
        <CardHeader>
          <CardTitle>Recent Transactions</CardTitle>
          <CardDescription>Latest invoices and their payment status.</CardDescription>
        </CardHeader>
        <CardContent>
           <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Invoice ID</TableHead>
                <TableHead>Patient</TableHead>
                <TableHead>Amount</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {transactions.map((t) => (
                <TableRow key={t.invoiceId}>
                  <TableCell className="font-medium">{t.invoiceId}</TableCell>
                  <TableCell>{t.patient}</TableCell>
                  <TableCell>{t.amount}</TableCell>
                   <TableCell>
                    <Badge variant={
                        t.status === 'Pending' ? 'secondary' : t.status === 'Overdue' ? 'destructive' : 'default'
                    } className={t.status === 'Paid' ? 'bg-green-500' : ''}>{t.status}</Badge>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
